:root{--mc: #026bfa;--sc: #026bfa;--lc: #f9f9f9;--dc: #444444;--gc: #dddddd}body{background-color:#fff;font-size:16px}.inner{position:relative;width:1200px;margin:0 auto}.cbtn{display:inline-block;padding:15px 40px;color:var(--lc);font-size:17px;background:linear-gradient(50deg, rgb(3, 134, 245) 0%, rgb(24, 207, 192) 100%);border-radius:30px;margin:0 0 40px 0}.cbtn:hover{filter:brightness(1.1)}.cbtn ::after{content:"";font-family:xeicon;display:inline-block;margin:0 0 0 10px;font-size:10px;vertical-align:1px}.csc{position:relative;padding:100px 0}.csc h2{font-size:35px;font-weight:700;margin:0 0 20px 0;text-align:center}.csc p{font-size:17px;margin:0 0 40px 0;text-align:center}@media(max-width: 768px){.Wrapper{overflow-x:hidden}.inner{width:auto;margin:0 auto}body{background-color:#333}}.Header{position:fixed;top:0;left:0;width:100vw;z-index:9999}.Header.on .hd_wrap{background:#000;color:var(--lc);line-height:100px}.Header.on .hd_wrap h1 a{background:url(../../public/assets/images/logo_w.png)}.Header.on .hd_wrap:hover h1 a{background:url(../../public/assets/images/logo_h.png)}.Header h1{position:absolute;top:0;left:50%;transform:translateX(-50%);font-size:0;z-index:999}.Header .hd_wrap{position:relative;color:var(--dc);line-height:100px;transition:.5s;background:var(--lc)}.Header .hd_wrap:hover{background:var(--lc);color:var(--dc)}.Header .hd_wrap:hover h1 a{background:url(../../public/assets/images/logo_h.png)}.Header .hd_wrap h1 a{display:inline-block;font-size:0;width:250px;height:37px;background:url(../../public/assets/images/logo.png);vertical-align:middle}.Header .top_service{position:absolute;top:0;right:70px;display:flex;gap:40px}.Header .top_service .top_box .top_search{position:relative;font-size:19px;float:left;margin:0 20px}.Header .top_service .top_box .top_search strong{cursor:pointer}.Header .top_service .top_box .top_search .search_box{display:none;position:absolute;top:80px;right:-65px;line-height:1;border:1px solid var(--gc);background:var(--lc);padding:30px 50px;width:600px;text-align:center}.Header .top_service .top_box .top_search .search_box input[type=text]{font-size:17px;width:450px;border:none;outline:none;border-bottom:2px solid var(--mc);background:var(--lc)}.Header .top_service .top_box .top_search .search_box button{background:rgba(0,0,0,0);border:none;outline:none;font-size:30px;cursor:pointer}.Header .top_service .top_box .top_search .search_box::before{background:#f9f9f9;background:var(--lc);border-left:1px solid #ddd;border-left:1px solid var(--gc);border-top:1px solid #ddd;border-top:1px solid var(--gc);content:"";height:20px;position:absolute;right:60px;top:-10px;-webkit-transform:rotate(45deg);transform:rotate(45deg);width:20px}.Header .top_service .top_box .top_search.on .search_box{display:block}.Header .top_service .top_box .lang{position:relative;float:left;margin:0 20px 0 0;cursor:pointer}.Header .top_service .top_box .lang :hover{color:var(--mc)}.Header .top_service .top_box .lang.on .arrow{transform:rotate(180deg)}.Header .top_service .top_box .lang.on .lang_box{display:block}.Header .top_service .top_box .lang .arrow{font-size:7px;vertical-align:.3em}.Header .top_service .top_box .lang .lang_box{display:none;position:absolute;top:70px;left:50%;transform:translateX(-50%);line-height:2;border:2px solid var(--gc);background:var(--lc);color:#444;padding:10px 10px;width:120px;text-align:center;border-radius:0px}.Header .top_service .top_box .lang .lang_box li :hover{color:var(--mc)}.Header .top_service .project li{display:inline-block;font-weight:400;font-size:15px}.Header .top_service .project li~li::before{content:"";display:inline-block;width:1px;height:10px;background:var(--gc);margin:0 10px}.Header .top_service strong :hover{color:var(--mc)}.Header .Gnb{position:relative;width:1200px;margin:0 auto}.Header .Gnb ul{display:flex;gap:40px}.Header .Gnb ul li{font-size:17px;font-weight:500}