:root{--mc: #026bfa;--sc: #026bfa;--lc: #f9f9f9;--dc: #444444;--gc: #dddddd}body{background-color:#fff;font-size:16px}.inner{position:relative;width:1200px;margin:0 auto}.cbtn{display:inline-block;padding:15px 40px;color:var(--lc);font-size:17px;background:linear-gradient(50deg, rgb(3, 134, 245) 0%, rgb(24, 207, 192) 100%);border-radius:30px;margin:0 0 40px 0}.cbtn:hover{filter:brightness(1.1)}.cbtn ::after{content:"";font-family:xeicon;display:inline-block;margin:0 0 0 10px;font-size:10px;vertical-align:1px}.csc{position:relative;padding:100px 0}.csc h2{font-size:35px;font-weight:700;margin:0 0 20px 0;text-align:center}.csc p{font-size:17px;margin:0 0 40px 0;text-align:center}@media(max-width: 768px){.Wrapper{overflow-x:hidden}.inner{width:auto;margin:0 auto}body{background-color:#333}}.MainSlider{background:#fff;padding:100px 0 0 0}.MainSlider .container{position:relative}.MainSlider .container .box{position:absolute;top:250px;left:50%;margin:0 0 0 -540px;width:350px;box-shadow:10px 10px 20px rgba(0,0,0,.075);background:#fff;padding:50px 40px;border-radius:30px;z-index:999}.MainSlider .container .box .tit{font-size:25px;margin:0 0 20px 0}.MainSlider .container .box p{font-size:15px;font-weight:300;min-height:120px;text-align:left;line-height:1.45;word-break:keep-all}.MainSlider .container .box .num{font-size:40px;font-weight:500;color:var(--mc)}.MainSlider .container .box .num span{font-size:20px;font-weight:300;color:var(--dc)}.MainSlider .container .arrows i{cursor:pointer;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);font-size:80px;color:var(--lc);z-index:999}.MainSlider .container .arrows i:first-child{margin:0 0 0 -750px}.MainSlider .container .arrows i:last-child{margin:0 0 0 750px}.MainSlider .container .dots{text-align:center;font-size:0;transform:translateY(-150px)}.MainSlider .container .dots li{display:inline-block;width:4px;height:10px;background:var(--lc);margin:10px;vertical-align:middle}.MainSlider .container .dots li.on{height:30px;background:#fff}.MainSlider .container .MainSlider_slide figure{height:calc(78vh - 155px);filter:grayscale(1)}.MainSlider .container .MainSlider_slide figure.on{filter:grayscale(0);transition:all 500ms ease-in-out}.MainSlider .container .MainSlider_slide figure img{margin:0 auto}.MainSlider .boxx{width:100vw;height:300px;border-radius:0;position:absolute;overflow:hidden;transform:translate3d(0, 0, 0);top:43%;z-index:1}.MainSlider .boxx .wave{opacity:1;position:absolute;top:419%;left:-275%;background:#fff;width:13000px;height:13000px;margin-left:-250px;margin-top:-250px;transform-origin:50% 48%;border-radius:44%;animation:drift 12000ms infinite linear;overflow:hidden}.MainSlider .boxx .wave.-three{animation:drift 20000ms infinite linear;opacity:.9;background:#fff}.MainSlider .boxx .wave.-two{animation:drift 25000ms infinite linear;opacity:.8;background:#fff}@keyframes drift{from{transform:rotate(0deg)}from{transform:rotate(360deg)}}