:root{--mc: #026bfa;--sc: #026bfa;--lc: #f9f9f9;--dc: #444444;--gc: #dddddd}body{background-color:#fff;font-size:16px}.inner{position:relative;width:1200px;margin:0 auto}.cbtn{display:inline-block;padding:15px 40px;color:var(--lc);font-size:17px;background:linear-gradient(50deg, rgb(3, 134, 245) 0%, rgb(24, 207, 192) 100%);border-radius:30px;margin:0 0 40px 0}.cbtn:hover{filter:brightness(1.1)}.cbtn ::after{content:"";font-family:xeicon;display:inline-block;margin:0 0 0 10px;font-size:10px;vertical-align:1px}.csc{position:relative;padding:100px 0}.csc h2{font-size:35px;font-weight:700;margin:0 0 20px 0;text-align:center}.csc p{font-size:17px;margin:0 0 40px 0;text-align:center}@media(max-width: 768px){.Wrapper{overflow-x:hidden}.inner{width:auto;margin:0 auto}body{background-color:#333}}.Best{background:var(--lc)}.Best h2{color:#333}.Best p{color:#333;margin:0 0 50px 0}.Best .des{display:none}.Best .container{display:flex;width:calc(1200px + (100% - 1200px)/2);margin:0 0 0 auto}.Best .container .left{width:450px;min-width:0}.Best .container .left .box{margin:0 0 30px 0;width:400px;box-shadow:10px 10px 20px rgba(0,0,0,.2)}.Best .container .right{flex-direction:column;min-width:0;flex:1;display:flex}.Best .container .right h2{padding:40px 0 0 0;color:#333;text-align:left;font-size:21px}.Best .container .right p{color:#333;text-align:left}.Best .container .right .slide{overflow:hidden;margin:auto 0 0 0}.Best .container .right_slide{width:1300px}.Best .container .right_slide figure{padding:0 20px 0 0}.Best .container .box{margin:0 0 30px 0}.Best .container .arrows{display:inline-block;color:var(--gc);cursor:pointer}.Best .container .arrows i{position:absolute;top:35%;left:50%;transform:translate(-50%, -50%);padding:10px;font-size:80px}.Best .container .arrows i:first-child{margin:0 0 0 -750px}.Best .container .arrows i:last-child{margin:0 0 0 750px}