:root{--mc: #026bfa;--sc: #026bfa;--lc: #f9f9f9;--dc: #444444;--gc: #dddddd}body{background-color:#fff;font-size:16px}.inner{position:relative;width:1200px;margin:0 auto}.cbtn{display:inline-block;padding:15px 40px;color:var(--lc);font-size:17px;background:linear-gradient(50deg, rgb(3, 134, 245) 0%, rgb(24, 207, 192) 100%);border-radius:30px;margin:0 0 40px 0}.cbtn:hover{filter:brightness(1.1)}.cbtn ::after{content:"";font-family:xeicon;display:inline-block;margin:0 0 0 10px;font-size:10px;vertical-align:1px}.csc{position:relative;padding:100px 0}.csc h2{font-size:35px;font-weight:700;margin:0 0 20px 0;text-align:center}.csc p{font-size:17px;margin:0 0 40px 0;text-align:center}@media(max-width: 768px){.Wrapper{overflow-x:hidden}.inner{width:auto;margin:0 auto}body{background-color:#333}}.Guide .inner{display:flex}.Guide .inner figure{flex:1;text-align:center}.Guide .inner figure:hover img{transform:scale(1.2)}.Guide .inner figure img{transition:.5s}.Guide .inner .box{margin:0 0 40px 0;overflow:hidden}.Guide .inner .tit{position:relative;font-size:20px;font-weight:500;margin:0 0 20px 0}.Guide .inner .dec{font-size:15px;margin:0 0 30px 0;line-height:1.45;display:inline-block;width:70%;min-height:50px;word-break:keep-all}